.App {
  text-align: center;
}

.menu {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: 2em;
	background-color: lightgray;
}

.dungeonDisplay {
	position: absolute;
	top: 2em;
	left: 0;
	right: 0;
	bottom: 0;

	background-color: #ccc;
}


.dungeonDisplay > canvas {
	background-color: white;
	outline: solid 5px #666;
}


